import React, { MouseEvent, useCallback, useEffect, useRef, useState } from 'react';
import { PostList } from '../../community/search/ResultSection/PostList/PostList';
import { API_URL } from '../../../renderer/CONSTANT_URL';
import type { UserInfo } from '../../../server/models/User';
import type { Post } from '../../../server/models/Post';

const arrSummary = ['ÏûëÏÑ±Ìïú Í∏Ä', 'ÎåìÍ∏Ä Îã® Í∏Ä'];

export function MyCommunitySection({ userInfo }: { userInfo: UserInfo }) {
    const [showSection, setShowSection] = useState<string>(window.localStorage.getItem('#2') || 'ÏûëÏÑ±Ìïú Í∏Ä');
    const [posts, setPosts] = useState<Post[] | null>(null);

    const [summaryCounts, setSummaryCounts] = useState<{ [key: string]: number }>({ ÏûëÏÑ±ÌïúÍ∏Ä: 0, ÎåìÍ∏ÄÎã®Í∏Ä: 0 });

    const { nickName } = userInfo;

    const listsRef = useRef<Array<HTMLLIElement | null>>([]);

    useEffect(() => {
        const $2 = window.localStorage.getItem('#2');
        const lists = [...document.querySelectorAll('.list-summary-community')] as Array<HTMLLIElement>;

        listsRef.current = lists;

        if ($2) {
            const index = arrSummary.indexOf($2);

            if (index !== -1) {
                lists[index].classList.add('on');
                setShowSection($2);
            }
        } else {
            lists[0].classList.add('on');
            setShowSection('ÏûëÏÑ±Ìïú Í∏Ä');
        }

        getMyPost(nickName).then((data) => {
            setSummaryCounts((prevCounts) => ({
                ...prevCounts,
                ÏûëÏÑ±ÌïúÍ∏Ä: data.posts.length,
            }));
        });

        getMyComment(nickName).then((data) => {
            setSummaryCounts((prevCounts) => ({
                ...prevCounts,
                ÎåìÍ∏ÄÎã®Í∏Ä: data.posts.length,
            }));
        });
    }, []);

    useEffect(() => {
        if (showSection === 'ÏûëÏÑ±Ìïú Í∏Ä') {
            getMyPost(nickName).then((data) => {
                if (data.success === true) {
                    if (data.posts) {
                        setPosts(data.posts);
                    } else setPosts(null);
                } else setPosts(null);
            });
        } else if (showSection === 'ÎåìÍ∏Ä Îã® Í∏Ä') {
            getMyComment(nickName).then((data) => {
                if (data.success === true) {
                    if (data.posts) {
                        setPosts(data.posts);
                    } else setPosts(null);
                } else setPosts(null);
            });
        }
    }, [showSection]);

    function initializeListStyle() {
        listsRef.current.forEach((list) => {
            if (list) list.classList.remove('on');
        });
    }

    const getMyPost = useCallback(async (nickName: string) => {
        const res = await fetch(`${API_URL}/posts/my?boundary=post&owner=${nickName}`);
        const data = await res.json();

        return data;
    }, []);

    const getMyComment = useCallback(async (nickName: string) => {
        const res = await fetch(`${API_URL}/posts/my?boundary=comment&owner=${nickName}`);
        const data = await res.json();

        return data;
    }, []);

    function handleListClick(event: MouseEvent<HTMLElement>, type: string) {
        const clickedNode = event.currentTarget;

        initializeListStyle();
        clickedNode.classList.add('on');

        if (type === 'ÏûëÏÑ±Ìïú Í∏Ä') {
            getMyPost(nickName).then((data) => {
                if (data.success === true) {
                    if (data.posts) {
                        setPosts(data.posts);
                    } else {
                        setPosts(null);
                    }
                } else {
                    setPosts(null);
                }
            });
        } else if (type === 'ÎåìÍ∏Ä Îã® Í∏Ä') {
            getMyComment(nickName).then((data) => {
                if (data.success === true) {
                    if (data.posts) {
                        setPosts(data.posts);
                    } else {
                        setPosts(null);
                    }
                } else {
                    setPosts(null);
                }
            });
        }

        if (clickedNode.lastElementChild?.innerHTML) {
            window.localStorage.setItem('#2', clickedNode.lastElementChild?.innerHTML);
        }
    }

    return (
        <section>
            <h3 className="sr-only">ÎÇ¥Í∞Ä Ïì¥ Í≤åÏãúÍ∏Ä Î∞è ÎåìÍ∏Ä</h3>
            <ul className="ul-summary-community">
                <li
                    className={`list-summary-community ${showSection === 'ÏûëÏÑ±Ìïú Í∏Ä' ? 'on' : ''}`}
                    onClick={(event) => handleListClick(event, 'ÏûëÏÑ±Ìïú Í∏Ä')}
                >
                    <p>{summaryCounts['ÏûëÏÑ±ÌïúÍ∏Ä']}</p>
                    <p>ÏûëÏÑ±Ìïú Í∏Ä</p>
                </li>
                <li
                    className={`list-summary-community ${showSection === 'ÎåìÍ∏Ä Îã® Í∏Ä' ? 'on' : ''}`}
                    onClick={(event) => handleListClick(event, 'ÎåìÍ∏Ä Îã® Í∏Ä')}
                >
                    <p>{summaryCounts['ÎåìÍ∏ÄÎã®Í∏Ä']}</p>
                    <p>ÎåìÍ∏Ä Îã® Í∏Ä</p>
                </li>
            </ul>
            {posts && posts.length > 0 ? (
                <PostList posts={posts} limit={20} />
            ) : (
                <div className="style-wrapper-no-review">
                    <div className="txt-no-review">üò≠</div>
                    <p>Î™©Î°ùÏù¥ ÏóÜÏñ¥Ïöî.</p>
                </div>
            )}
        </section>
    );
}
